<template>
  <div class="main">
    <div class="box">
      <div class="title">{{ userText.title }}</div>
      <div class="text">{{ userText.text }}</div>
      <div class="time">发布时间 {{ userText.time }}</div>
      <div class="lasttime">上次修改时间 {{ lasttimex }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "userid",
  props: ["userID"],
  computed: {
    userText() {
      let oldData = JSON.parse(localStorage.getItem("userdata"));
      let newData = { text: "", title: "", time: "" };
      JSON.parse(localStorage.getItem("userdata")).forEach((item, index) => {
        if (item.id == this.userID) {
          newData.text = oldData[index].text;
          newData.title = oldData[index].title;
          newData.time = oldData[index].SendTime.sendtime;
        }
      });
      return newData;
    },
    lasttimex() {
      //获取当前的年月日
      let dates = new Date();
      let year = dates.getFullYear();
      let month = dates.getMonth() + 1;
      let day = dates.getDate();

      return `${year}-${month}-${day}`;
    },
  },
  mounted() {
    this.$bus.$emit("showitem", true);
    this.$bus.$on("rewrite", (data) => {
      if (data) {
        this.inputshow = true;
      } else {
        this.inputshow = false;
      }
    });
  },
  beforeDestroy() {
    this.$bus.$emit("default", false);
  },
};
</script>

<style scoped>
.main {
  width: 100%;
  font: 25px "";
  min-height: 100vh;
  box-sizing: border-box;
  padding: 20px;
}
.box {
  width: 80%;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0.5em;
}
.title {
  font: 700 35px "";
  display: flex;
}
.text {
  margin-left: 2em;
  margin-top: 20px;
  border: 1px solid black;
  box-sizing: border-box;
  padding: 1em;
}
.time {
  display: flex;
  justify-content: flex-end;
}
.lasttime {
  font: 700 14px "";
  display: flex;
  justify-content: flex-end;
}
</style>
